import React, { Component } from 'react'
import { withFirebase } from '../components/FirebaseContext'

class FetchingExample extends Component {
  state = {
    names: null,
    //score: null,
  }
  componentDidMount() {
    const { firebase } = this.props
    firebase
      .database()
      .ref('/1bGTK9Z37gURouC3Iqbj323dgplH_y8rTIq7nRYaxIyQ')
      .once('value')
      .then(snapshot => {
        this.setState({
          names: snapshot.val().task1['1']['Name'],
        //#score: snapshot.val().task1['1']['Score'],
        })
      })
  }
  render() {
    const { names } = this.state;
    //const { score } = this.state;


    if (!names) {
      return null
    }

    let rows = [];
    for (let i = 0; i < names.length; i++) {
        // note: we add a key prop here to allow react to uniquely identify each
        // element in this array. see: https://reactjs.org/docs/lists-and-keys.html
        rows.push(<tr key={i}>{names[i]}</tr>);
    }

    return (
      <table>
        <tbody>
          {rows}
        </tbody>
      </table>
    )

  }
}

export default withFirebase(FetchingExample)
